.ModalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1040;
  background-color: #000;
  opacity: 0.5;
}

.ModalWrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  width: 100%;
  height: 100%;
}

.Modal {
  z-index: 100;
  background: white;
  margin: 3rem auto;
  max-width: 350px;
  padding: 2rem;
  border: none;
}

.ModalHeader {
  display: flex;
  justify-content: center;
  font-size: 1.3rem;
}

.ModalContent{
  padding: 1rem 0;
}

.ModalFooter{
  display: flex;
  justify-content: flex-end;
}

.ConfirmButton{
  font-size: 1.1rem;
  padding: .3rem 1rem;
  margin-left: .5rem;
  font-weight: 700;
  line-height: 1;
  opacity: 0.5;
  cursor: pointer;
  border: none;
}

.ConfirmButton:hover{
  background-color: antiquewhite;
  opacity: 1;
}